<template>
<div class="card">
  <header class="card-header">
    <p class="card-header-title is-size-4">
      Comments:
    </p>
  </header>
  <div class="card-content">
    <div class="content">
        <table class="table is-striped" style="word-break:break-all;">
            <thead>
                <th width="6%">ID</th>
                <th width="30%">Content</th>
                <th width="12%">Name</th>
                <th width="12%">Create at</th>
                <th width="6%">PID</th>
                <th width="14%">Email</th>
                <th width="12%">Site</th>
                <th width="8%">Operations</th>
            </thead>
            <tbody>
                <tr v-for="(comment) in comments" :key="comment.id">
                    <th>{{comment.id}}</th>
                    <td>{{comment.content}}</td>
                    <td>{{comment.name}}</td>
                    <td>{{formatDate(comment.create_time)}}</td>
                    <td>{{comment.pid}}</td>
                    <td>{{comment.email}}</td>
                    <td>{{comment.site}}</td>
                    <td><a @click="deleteById(comment.id)">Delete</a></td>
                </tr>
            </tbody>
        </table>
    </div>
  </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            comments:{},
        }
    },

    methods: {
        deleteById(id) {
            this.$axios.post('/commentsmgmt/delete',{
                id:id
            }).then((response)=>{
                console.log(response)
                this.onLoad();
                this.$notify('Done.')
            })
        },

        formatDate(timestamp) {
            let date = this.$moment.unix(timestamp);
            return date.format('YYYY/MM/DD HH:mm:ss')
        },

        onLoad() {
            this.$axios.get('/commentsmgmt').then(resp => {
            console.log(resp.data)
            this.comments = resp.data.data;
            })
        }


    },

    created() {
        this.onLoad();
    }
    
}
</script>